import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Button, Group, Div, PanelHeader, FormLayout, FormLayoutGroup, Input, InfoRow, Radio, Select } from '@vkontakte/vkui';
import costs from './costs'
import './Home.css';

class Home extends React.Component {  
    
    constructor(props) {  
        super(props);  
        var 
        	curdate = new Date,
        	hours = curdate.getHours(),
        	hours = hours > 9? hours : '0'+hours,
        	minutes = curdate.getMinutes(),
        	minutes = minutes > 9? minutes : '0'+minutes;

        this.state = {  
        	id: "home",
            result: "",
            currentTime: hours + ":" + minutes,
            currentBus: "",
            typecost: "cash"
        };  
        this.updateMessage = this.updateMessage.bind(this);  
        this.onChangeTime = this.onChangeTime.bind(this); 
        this.onChangeBus = this.onChangeBus.bind(this); 
        this.onChangeTypeCost = this.onChangeTypeCost.bind(this); 
        this.rubtostr = this.rubtostr.bind(this); 
    }
   	updateMessage = (e) => {
   		this.setState(state => ({
	      result: ""
	    }));
   		var rez = 0;
   		var objbus = costs[this.state.currentBus];
   		if (objbus) {
   			var objday = objbus[this.state.typecost];
   			if (objday) {
   				var times = this.state.currentTime.split(":");
   				var curdate = new Date, curdate2 = new Date;
   				curdate.setHours(times[0], times[1]);
   				curdate2.setHours(21, 0);		  				
   				if (curdate2 - curdate)
   					rez = objday.a;
   				else
   					rez = objday.b;
   			}
   		}

   		rez = rez == 0? "Нет данных" : rez + ' ' + this.rubtostr(rez);
   		if (objbus && objbus.isAfter10)
   			rez += "*"
		this.setState(state => ({
	      result: rez
	    }));
	}
	onChangeTime = (e) => {
		this.setState({ currentTime: e.target.value });
	}
	onChangeBus = (e) => {
		this.setState({ currentBus: e.target.value });
	}	
	onChangeTypeCost = (e) => {
		this.setState({ typecost: e.target.value });
	}	
	rubtostr = (n) => {
		var text_forms = ['рубль', 'рубля', 'рублей'];
	  	n = Math.abs(n) % 100;
	  	var n1 = n % 10;
	  	if (n > 10 && n < 20) return (text_forms[2]);
	  	if (n1 > 1 && n1 < 5) return (text_forms[1]);
	  	if (n1 == 1) return (text_forms[0]);
	  	return (text_forms[2]);
	}
    render() {  
         return (  
           <Panel id={this.state.id} theme="white">
           		<PanelHeader>
            		Стоимость общественного транспорта в Пензе
          		</PanelHeader>
          	<FormLayout>
		      <FormLayoutGroup top="Номер маршрутки/автобуса/троллейбуса">
		      	<Input type="text" alignment="center" placeHolder="Введите номер"  onChange={this.onChangeBus} />
		       </FormLayoutGroup>
		       <FormLayoutGroup top="Время">
		        <Input type="text" defaultValue={this.state.currentTime} alignment="center" onChange={this.onChangeTime} />
		      </FormLayoutGroup>
		      <FormLayoutGroup top="Тип оплаты">
	                <div  onChange={this.onChangeTypeCost}>
						<Radio type="radio" value="cash" defaultChecked name="typecost">Наличные</Radio>
						<Radio type="radio" value="card" name="typecost">Карта</Radio>
			        </div>
	          </FormLayoutGroup>
	          <Button size="xl" level="primary" stretched="true" onClick={this.updateMessage}>Сколько стоит проезд?</Button>
		      <Div alignment="center">
			      <InfoRow className="resultcost">
			      	<center>{this.state.result}</center>
			      </InfoRow>
		      </Div>
		      </FormLayout>
           </Panel> 
        )  
    }  
}

 export default Home;